html {
  color: #161616;
  scroll-behavior: smooth;
  font-family: Cinzel, serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 27px;
}

body, h1, h2, h3, h4, p, input {
  margin: 0;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.grid {
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;
  display: grid;
}

.grid__mobile {
  grid-template-columns: repeat(3, 1fr);
  column-gap: 8px;
  display: grid;
}

.grid__item-mobile-1 {
  grid-column: 1 / 1;
}

.grid__item-mobile-2 {
  grid-column: 2 / 2;
}

.grid__item-mobile-3 {
  grid-column: 3 / 3;
}

@media (width >= 744px) {
  .grid {
    grid-template-columns: repeat(6, 1fr);
    column-gap: 24px;
    display: grid;
  }
}

@media (width >= 1024px) {
  .grid {
    grid-template-columns: repeat(12, 1fr);
    column-gap: 24px;
    display: grid;
  }
}

