.container {
  max-width: 1020px;
  margin: 0 auto;
  padding: 0 20px;
}

@media (width >= 744px) {
  .container {
    padding: 0 72px;
  }
}

@media (width >= 1024px) {
  .container {
    padding: 0 120px;
  }
}

.header {
  background-image: url("header.2c9d58a4.png");
  background-position: center;
  background-size: cover;
}

.header__content {
  box-sizing: border-box;
  grid-template-rows: 1fr 2fr;
  height: 100vh;
  padding: 25px 0 200px;
  display: grid;
}

@media (width >= 744px) {
  .header__content {
    padding-bottom: 10px;
  }
}

.header__top {
  box-sizing: border-box;
  grid-template-columns: 1fr 18px 18px;
  align-items: center;
  gap: 30px;
  height: min-content;
  display: grid;
}

@media (width >= 744px) {
  .header__top {
    grid-template-columns: 1fr 24px 24px;
    gap: 62px;
    display: grid;
  }
}

.header__logo-img {
  width: 64px;
  height: 46px;
}

@media (width >= 744px) {
  .header__logo-img {
    width: 86px;
    height: 65px;
  }
}

.header__logo-link {
  width: max-content;
}

.header__title {
  text-align: center;
  color: #fff;
  letter-spacing: -1.5px;
  justify-content: center;
  font-size: 40px;
  font-weight: 700;
  line-height: 40px;
  display: -ms-flexbox;
  display: flex;
}

@media (width >= 744px) {
  .header__title {
    font-size: 64px;
    line-height: 64px;
  }
}

@media (width >= 1024px) {
  .header__title {
    font-size: 80px;
    line-height: 80px;
  }
}

.icon {
  cursor: pointer;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  display: block;
  position: relative;
}

.icon__tooltip-top {
  color: #fff;
  text-transform: uppercase;
  pointer-events: none;
  opacity: 0;
  width: max-content;
  padding-right: 20px;
  font-weight: 700;
  transition: opacity .3s;
  display: block;
  position: absolute;
  top: -30%;
  right: 100%;
  transform: translateY(-50%);
}

.icon:hover .icon__tooltip-top {
  opacity: 1;
  pointer-events: all;
}

.icon__tooltip-bottom {
  color: #fff;
  text-transform: uppercase;
  pointer-events: none;
  opacity: 0;
  width: max-content;
  padding: 8px 60px 0;
  font-weight: 700;
  transition: opacity .3s;
  display: block;
  position: absolute;
  top: 150%;
  right: 50%;
  transform: translateY(-30%);
}

.icon:hover .icon__tooltip-bottom {
  opacity: 1;
  pointer-events: all;
}

.icon__call {
  background-image: url("phone.10a2213c.svg");
  width: 18px;
  height: 18px;
}

@media (width >= 744px) {
  .icon__call {
    width: 24px;
    height: 24px;
  }
}

.icon--menu {
  background-image: url("menu.a3727caf.svg");
  width: 18px;
  height: 12px;
}

.icon--menu:hover {
  background-image: url("menu_hover.7722ce86.svg");
}

@media (width >= 744px) {
  .icon--menu {
    width: 24px;
    height: 16px;
  }
}

.icon--cross {
  background-image: url("cross.2d103320.svg");
  width: 16px;
  height: 16px;
  display: block;
}

.icon-cross--grid {
  grid-column: 3 / 3;
}

.menu {
  box-sizing: border-box;
  background-color: #d12d35;
  height: 100vh;
  padding: 25px 0;
  overflow-y: auto;
}

.menu__top {
  box-sizing: border-box;
  grid-template-columns: 1fr 18px 18px;
  align-items: center;
  gap: 30px;
  height: min-content;
  margin-bottom: 40px;
  display: grid;
}

@media (width >= 744px) {
