  font-weight: 700;
  line-height: 22px;
  display: inline-block;
}

@media (width >= 1024px) {
  .menu__link {
    border-bottom: 1px solid #0000;
    padding-bottom: 8px;
    transition: border-color .3s;
  }

  .menu__link:hover {
    border-color: #fff;
  }
}

.menu__call {
  text-transform: uppercase;
  color: #fff;
  width: max-content;
  font-weight: 700;
  display: block;
}

@media (width >= 1024px) {
  .menu__call {
