}

.menu__call--action {
  letter-spacing: 2px;
  padding: 8px 0;
  font-size: 12px;
  line-height: 16px;
}

.menu--open {
  transition: transform .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.menu--open:target {
  transform: translateX(0);
}

